<template>
  <v-app style="height: 100vh">
    <v-app-bar v-if="disableNavigation" dense>
      <v-toolbar-title>
        <v-icon x-small :color="socketStatusColor">mdi-checkbox-blank-circle</v-icon>
        <b style="margin-left: 6px">{{room ? room : "..."}}</b>
      </v-toolbar-title>
      <span
        style="margin-left: 12px"
      >Predplatené do: {{expiration ? new Date(expiration).toLocaleDateString() : "..."}}</span>
      <v-spacer></v-spacer>
      <v-btn icon>
        <v-icon>mdi-cog</v-icon>
      </v-btn>
    </v-app-bar>
    <router-view @login="onLogin" @socketStatus="onSocketStatusChange" style="max-height: 94.8%"></router-view>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data: () => ({
    room: null,
    expiration: null,
    socketStatusColor: "error"
  }),
  computed: {
    disableNavigation: function() {
      if (this.$route.path === "/login" || this.$route.path === "/register")
        return false;
      return true;
    }
  },
  methods: {
    onLogin(roomDetails) {
      this.room = roomDetails.room
      this.expiration = Number(roomDetails.expiration)
    },
    onSocketStatusChange(status) {
      if (status === 1) this.socketStatusColor = "success";
      else this.socketStatusColor = "error";
    }
  }
};
</script>
