<template>
  <div style="width: 100vw; height: 100vh; margin: 0;">
    <video src="../assets/video.mp4" poster="../assets/poster.jpg" autoplay loop playsinline muted></video>

    <div class="overlay">
      <div class="form">
        <v-form ref="form" v-model="valid">
          <h1>Prihlásenie</h1>
          <v-text-field
            :disabled="snackbar"
            v-model="roomName"
            :rules="[rules.required, rules.room]"
            label="Názov miestnosti"
            hint="Slúži na identifikáciu miestnosti"
            filled
            rounded
          ></v-text-field>
          <v-text-field
            :disabled="snackbar"
            filled
            rounded
            v-model="password"
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, rules.min]"
            :type="show1 ? 'text' : 'password'"
            label="Heslo miestnosti"
            hint="Slúži na vstup do miesnosti"
            @click:append="show1 = !show1"
          ></v-text-field>
          <v-text-field
            :disabled="snackbar"
            filled
            rounded
            v-model="adminPassword"
            :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[rules.required, rules.min]"
            :type="show2 ? 'text' : 'password'"
            label="Heslo administrátora"
            hint="Slúži na správu miestnosti"
            @click:append="show2 = !show2"
          ></v-text-field>
          <v-btn outlined :disabled="!valid" @click="login()">Prihlásiť sa</v-btn>
        </v-form>
      </div>
    </div>

    <v-snackbar v-model="snackbar" :timeout="timeout" multi-line>
      {{ snackbarText }}
      <v-btn
        v-if="snackbarText === 'Nesprávne meno alebo heslo.' ||
            snackbarText === 'Skupina neexistuje.' ||
            snackbarText === 'Zlá požiadavka'"
        text
        @click="reload()"
      >Skúsiť znova</v-btn>
      <v-btn v-else text @click="snackbar = false">Ok</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    roomName: "",
    password: "",
    adminPassword: "",
    show1: false,
    show2: false,
    rules: {
      required: v => !!v || "Povinné",
      min: v => v.length >= 8 || "Minimálne 8 znakov",
      room: v => /^\S*$/.test(v) || "Nesmmie obsahovať medzery"
    },
    snackbar: false,
    snackbarText: "",
    timeout: 0
  }),
  methods: {
    login: function() {
      this.valid = false;

      this.$http
        .post(`${this.$apiEndpoint}/login`, {
          room: this.roomName,
          password: this.password,
          adminPassword: this.adminPassword
        })
        .then(response => {
          if (
            response.data === "Nesprávne meno alebo heslo." ||
            response.data === "Skupina neexistuje." ||
            response.data === "Zlá požiadavka" ||
            response.data === "Nesprávne meno alebo heslo."
          ) {
            this.snackbarText = response.data;
            this.snackbar = true;
          } else {
            sessionStorage.setItem("authToken", response.data);
            this.$emit("login", this.$jwt_decode(response.data));
            this.$router.push("/");
          }
        })
        .catch(error => {
          this.snackbarText = "Vyskytla sa chyba, prosím skúste neskôr.";
          this.snackbar = true;
          console.error("Error:", error);
        });
    },
    reload() {
      this.valid = false;
      this.roomName = "";
      this.password = "";
      this.adminPassword = "";
      this.show1 = false;
      this.show2 = false;
      this.snackbar = false;
      this.snackbarText = "";
      this.timeout = 0;
    }
  }
};
</script>

<style>
video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  filter: opacity(0.2);
}

.overlay {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.form {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;
  top: 50%;
  transform: translateY(-50%);
}

.form h1 {
  margin-bottom: 24px;
}
</style>