<template>
  <div style="width: 100vw; height: 100vh; margin: 0;">
    <video src="../assets/video.mp4" poster="../assets/poster.jpg" autoplay loop playsinline muted></video>

    <div class="overlay">
      <div class="form">
        <h1>Registrácia</h1>
        <v-stepper v-model="step">
          <v-stepper-header>
            <v-stepper-step step="1" :complete="step > 1">Vytvorte názov</v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step step="2" :complete="step > 2">Overte email</v-stepper-step>

            <v-divider></v-divider>

            <v-stepper-step step="3">Pridajte zabezpečenie</v-stepper-step>
          </v-stepper-header>
          <v-stepper-content step="1">
            <v-form ref="form" v-model="valid1">
              <v-text-field
                v-model="roomName"
                :rules="[rules.required, rules.room]"
                label="Názov miestnosti"
                hint="Slúži na identifikáciu miestnosti"
                filled
                rounded
              ></v-text-field>
            </v-form>

            <v-btn outlined :disabled="!valid1" @click="step++">Ďalší krok</v-btn>
          </v-stepper-content>
          <v-stepper-content step="2">
            <v-form ref="form" v-model="valid2">
              <v-text-field
                :disabled="snackbar"
                v-model="email"
                :rules="[rules.required, rules.email]"
                label="Email"
                hint="Slúži len na overenie"
                filled
                rounded
              ></v-text-field>
            </v-form>
            <v-btn outlined :disabled="!valid2" @click="confirmEmail()">Overiť email</v-btn>
          </v-stepper-content>
          <v-stepper-content step="3">
            <v-form ref="form" v-model="valid3">
              <v-text-field
                filled
                rounded
                :disabled="snackbar"
                v-model="password"
                :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, rules.min]"
                :type="show1 ? 'text' : 'password'"
                label="Heslo miestnosti"
                hint="Slúži na vstup do miesnosti"
                @click:append="show1 = !show1"
              ></v-text-field>
              <v-text-field
                filled
                rounded
                :disabled="snackbar"
                v-model="adminPassword"
                :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[rules.required, rules.min]"
                :type="show2 ? 'text' : 'password'"
                label="Heslo administrátora"
                hint="Slúži na správu miestnosti"
                @click:append="show2 = !show2"
              ></v-text-field>
            </v-form>
            <v-btn outlined :disabled="!valid3" @click="signUp()">Dokončiť registráciu</v-btn>
          </v-stepper-content>
        </v-stepper>
      </div>
    </div>

    <v-snackbar v-model="snackbar" :timeout="timeout" multi-line>
      {{ snackbarText }}
      <v-btn v-if="snackbarText === 'Skupina vytvorená, môžete sa prihlásiť.'" text @click="$router.push('/login')">Prihlásiť sa</v-btn>
      <v-btn v-else-if="!snackbarText.includes('Overovací email odoslaný, prosím skontrolujte svoju schránku.')" text @click="reload()">Skúsiť znova</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  data: () => ({
    step: 1,
    valid1: false,
    valid2: false,
    valid3: false,
    roomName: "",
    email: "",
    snackbar: false,
    snackbarText: "",
    timeout: 0,
    password: "",
    adminPassword: "",
    show1: false,
    show2: false,
    rules: {
      required: v => !!v || "Povinné",
      min: v => v.length >= 8 || "Minimálne 8 znakov",
      room: v => /^\S*$/.test(v) || "Nesmmie obsahovať medzery",
      email: v => /.+@.+\..+/.test(v) || "Zadajte platnú emailovú adresu"
    }
  }),
  created: function() {
    if (this.$route.query.token) this.step = 3;
  },
  methods: {
    confirmEmail: function() {
      this.valid2 = false;

      this.$http
        .post(`${this.$apiEndpoint}/verify`, {
          email: this.email,
          room: this.roomName
        })
        .then(response => {
          //   this.snackbarText =
          //     "Overovací email odoslaný, prosím skontrolujte svoju schránku.";
          this.snackbarText = response.data;
          this.snackbar = true;
        })
        .catch(error => {
          this.snackbarText = "Vyskytla sa chyba, prosím skúste neskôr.";
          this.snackbar = true;
          console.error("Error:", error);
        });
    },
    signUp: function() {
      this.valid3 = false;

      this.$http
        .post(`${this.$apiEndpoint}/create`, {
          token: this.$route.query.token,
          password: this.password,
          adminPassword: this.adminPassword
        })
        .then(response => {
          this.snackbarText = response.data;
          this.snackbar = true;
        })
        .catch(error => {
          this.snackbarText = "Vyskytla sa chyba, prosím skúste neskôr.";
          this.snackbar = true;
          console.error("Error:", error);
        });
    },
    reload() {
      this.step = 1;
      this.valid2 = false;
      this.valid3 = false;
      this.valid1 = false;
      this.roomName = "";
      this.email = "";
      this.snackbar = false;
      this.snackbarText = "";
      this.timeout = 0;
      this.password = "";
      this.adminPassword = "";
      this.show1 = false;
      this.show2 = false;
      if (this.$route.query.token) this.$router.replace("/register");
    }
  }
};
</script>

<style>
video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  filter: opacity(0.2);
}

.overlay {
  position: relative;
  height: 100vh;
  width: 100vw;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.form {
  position: absolute;
  display: flex;
  justify-content: center;
  flex-direction: column;
  top: 50%;
  transform: translateY(-50%);
  width: 40%;
}

.form h1 {
  margin-bottom: 24px;
}

.theme--dark.v-stepper {
  background: none !important;
}

.v-stepper {
  border-radius: 0 !important;
  box-shadow: none !important;
}

.v-stepper__header {
  box-shadow: none !important;
}

@media (max-width: 1500px) {
  .form {
    width: 50%;
  }
}
@media (max-width: 1350px) {
  .form {
    width: 60%;
  }
}
@media (max-width: 1150px) {
  .form {
    width: 70%;
  }
}
@media (max-width: 1000px) {
  .form {
    width: 80%;
  }
}
@media (max-width: 450px) {
  .form {
    width: 90%;
  }
}
</style>